/*
 * An object containing properties for filter types
 */
export class FilterType {
  /**
   * Select type
   */
  static readonly SELECT: string = 'select';

  /**
   * Text type
   */
  static readonly TEXT: string = 'text';

  /**
   * Type ahead type
   */
  static readonly TYPEAHEAD: string = 'typeahead';
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
